import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import "react-toastify/dist/ReactToastify.css";
import "./Login.css";
import { object, string, } from "yup";
import { useDispatch, useSelector } from "react-redux";
import { authActions, employeeActions, notificationActions, permissionActions } from '../../../core/resource/actions';
import { notify } from "../../globalComponents/ToastNotification";
import { getDataFromLocalstorage, getLocalDataAsObject, setDataOnLocalstorage } from "../../../utils/AppHelper";
import Cookies from 'js-cookie';
import { decryptValue, encryptValue } from "../../../utils/common";
import { CustomButton } from "../../globalComponents";
import ForgotPasswordModal from "./ForgotPasswordModal";

const Login = (props) => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
    const {
      getMenuRoutesPermissionRes,
      getMenuRoutesPermissionError,
      getMenuRoutesPermissionPending
    } = useSelector(state => state.permission)
  const { isUserLoginPending, userLoginRes, userLoginResError } = useSelector(state => state.auth);
  const [showPassword, setShowPassword] = useState(false)
  const [isShowForgotPasswordModal, setIsShowForgotPasswordModal] = useState(false)
  const [formData, setFormData] = useState({
    email: "",
    password: "",
  });

  const [isOpen, setIsOpen] = useState(false);
  const [passwordShown, setPasswordShown] = useState(false);

  const togglePasswordVisiblity = () => {
    setPasswordShown(passwordShown ? false : true);
  };
  let FormSchema = object({
    email: string().required(),
    password: string().required(),
  });
  const saveUser = async () => {
    // await login({
    //   body: { email: formData?.email, password: formData?.password },
    // });
  };

  const handleOnLogin = async (filePayload = null) => {
    const params = {
      email: formData?.email,
      password: formData?.password,
    };
    dispatch(authActions.userLoginRequest(params));
  };

  useEffect(() => {
    handleLoginResposnse()
  }, [
    isUserLoginPending,
    userLoginRes,
    userLoginResError,
  ]);
       

  const handleLoginResposnse = async () => {
    if (!isUserLoginPending) {
      if (userLoginResError) {
        dispatch(authActions.handleClearAuthStore('HANDLE_CLEAR_LOGIN_STORE'))
        return;
      }
      if (userLoginRes) {
        if (userLoginRes?.code == 200) {
          let { token, user } = userLoginRes && userLoginRes?.data

          let encryptUser = encryptValue(JSON.stringify(user))
          await setDataOnLocalstorage(encryptUser, "user");
          await setDataOnLocalstorage(token, "authtoken");
          dispatch(employeeActions.employeeDetails(user))

          Cookies.set('authtoken', token);
          Cookies.set("authData", JSON.stringify(user));
          dispatch(permissionActions.getMenuRoutesPermissionRequest())
          dispatch(authActions.handleClearAuthStore('HANDLE_CLEAR_LOGIN_STORE'))

          if(user?.role_id ==="67fdf22e24286606d8499d9a"){
            navigate('/onboarding')
          }else{
            navigate('/')
          }
  
          

        }

      }
    }
  }
  
 

  const handleFormdata = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  const isViewModalOpen = () => {
    setIsOpen(!isOpen);
  };

  const toggleForgotPasswordModal = () => {
    setIsShowForgotPasswordModal(!isShowForgotPasswordModal)
  }

  return (

    <div className="mittContent">
      <div className="glueplelogo">
        <a href="">
          <img src="images/glueple-logo.png" alt="" width={120} />
        </a>
      </div>
      <div className="inner-content">
        <div className="leftContent">
          <figure className="position-relative">
            <img src="images/img.png" alt="" />
            <span className="icon1">
              {" "}
              <img src="images/attendance.png" alt="" width={70} />
            </span>
            <span className="icon2">
              {" "}
              <img src="images/human-resources.png" alt="" width={70} />
            </span>
            <span className="icon3">
              {" "}
              <img src="images/payroll.png" alt="" width={70} />
            </span>
            <span className="icon4">
              {" "}
              <img src="images/hiring.png" alt="" width={60} />
            </span>
            <span className="icon5">
              {" "}
              <img src="images/human-resources-srch.png" alt="" width={60} />
            </span>
            <span className="icon6">
              {" "}
              <img src="images/market-analysis.png" alt="" width={65} />
            </span>
            <span className="icon7">
              <img src="images/employee.png" alt="" width={50} />
              Streamline Your Workforce
            </span>
          </figure>
        </div>
        <div className="rightTxt position-relative">
          <div className="loginFormBox">
            <div className="text-start">
              <h3 className="mb-2">Login</h3>
              <p className="">
                Human Resource Management System
              </p>
            </div>
            <form>
              <div className="mb-4">
                <label className="form-label">Enter Email or Employee ID</label>
                <input
                  className="form-control w-100"
                  type="email"
                  placeholder="Email / Employee ID"
                  name="email"
                  onChange={(e) => handleFormdata(e)}
                  value={formData.email}
                />
              </div>
              <div className="mb-1">
                <label className="form-label">Password</label>
                <div className="position-relative">
                  <input
                    className="form-control w-100 pe-5"
                    type={showPassword ? 'text' : 'password'}
                    placeholder="Enter Password"
                    name="password"
                    value={formData.password}
                    onChange={(e) => handleFormdata(e)}
                  />
                  <span className="eyeInput" onClick={() => setShowPassword(!showPassword)}>
                    {showPassword ? <img src="images/eye-open.svg" alt="" width={20} /> : <img src="images/eye-hide.svg" alt="" width={20} />}
                  </span>
                </div>
              </div>
              <div className="forgotTxt">
                <div className="customCheckbox">
                  <input type="checkbox" id="termCheck" />
                  <label htmlFor="termCheck">Remember me</label>
                </div>
                {/* <span onClick={() => toggleForgotPasswordModal()}>Forgot Password</span> */}
              </div>
              <div className="text-center my-2 mt-4">
                <CustomButton
                  text="Login"
                  isLoading={isUserLoginPending}
                  classNames={'btn btn-login'}
                  handleClick={handleOnLogin}
                />
              </div>
              <strong className="fs-8 mt-4 text-center d-block">
                Â© Copyright Powered by @QDegrees.com
              </strong>
              <a href="https://www.qdegrees.com" target="_blank" className="text-center d-block">
                <img src="images/qdegreesLogo.png" alt="" width={80} />
              </a>
            </form>
          </div>
        </div>
      </div>

      {isShowForgotPasswordModal &&<ForgotPasswordModal
        isShow={isShowForgotPasswordModal}
        toggleForgotPasswordModal={toggleForgotPasswordModal}
      />}

    </div>

  );
};

export default Login;
