import React, { useEffect, useState } from 'react'
import { CustomButton, CustomError } from '../../globalComponents'
import { useCustomTranslation } from '../../../translation'
import { Input, Label } from '../../commanComponent'
import { validateSchema } from '../../../utils/ValidationHelper'
import OtpInput from 'react-otp-input';
import { resetPasswordSchema } from '../../../utils/ValidationSchema'
import { useDispatch, useSelector } from 'react-redux'
import { authActions } from '../../../core/resource/actions'

const ForgotPasswordModal = (
    {
        isShow,
        btnLeftLoading = false,
        btnRightLoading = false,
        btnTextLeft,
        btnTextRight,
        toggleForgotPasswordModal
    }
) => {
    const dispatch = useDispatch();
    const {
        forgotPasswordPending,
        forgotPasswordRes,
        forgotPasswordError,

        sendOtpPending,
        sendOtpRes,
        sendOtpError,

        verifyOtpPending,
        verifyOtpRes,
        verifyOtpError,

    } = useSelector(state => state.auth);
    const { t } = useCustomTranslation()
    const [otpSent, setOtpSent] = useState(false)
    const [buttonOTP, setButtonOTP] = useState(false);
    const [otpVerify, setOtpVerify] = useState(false)
    const [email, setEmail] = useState('')
    const [otp, setOtp] = useState('')
    const [newPassword, setNewPassword] = useState('')
    const [confirmPassword, setConfirmPassword] = useState('')
    const [passwordShown, setPasswordShown] = useState(false);
    const [passwordShown1, setPasswordShown1] = useState(false);
    const [minutes, setMinutes] = useState(0);
    const [seconds, setSeconds] = useState(0);

    const sendOtpFun = async () => {
        let data = {
            email_mobile: email
        }

        dispatch(authActions.sendOtpRequest(data))
    }

    useEffect(() => {
        if (!sendOtpPending) {
            if (sendOtpError) {
                dispatch(authActions.handleClearAuthStore("HANDLE_CLEAR_SEND_OTP_STORE"))
                return;
            }
            if (sendOtpRes) {
                if (sendOtpRes?.code === 200) {
                    dispatch(authActions.handleClearAuthStore("HANDLE_CLEAR_SEND_OTP_STORE"))
                    setOtpSent(true)
                    setMinutes(0)
                    setSeconds(59)

                }
            }
        }
    }, [
        sendOtpRes,
        sendOtpError,
        sendOtpPending
    ]);

    const onClose = () => {
        toggleForgotPasswordModal();
        setOtpSent(false);
        setOtpVerify(false);
        setButtonOTP(false);
        setOtp('');
        setEmail('')
    };

    const onEmailChange = (e) => {
        setEmail(e.target.value)
        setOtpSent(false);
        setOtpVerify(false);
    };

    const checkOtp = async e => {
        setOtp(e);
        if (e.length === 6) {
            let data = {
                email_mobile: email,
                otp: e,
                otp_id: ''
            };
            dispatch(authActions.verifyOtpRequest(data))

        }
    };

    useEffect(() => {
        if (!verifyOtpPending) {
            if (verifyOtpError) {
                dispatch(authActions.handleClearAuthStore("HANDLE_CLEAR_VERIFY_OTP_STORE"))
                setButtonOTP(false);
                setOtpVerify(false);
                return;
            }
            if (verifyOtpRes) {
                if (verifyOtpRes?.code === 200) {
                    dispatch(authActions.handleClearAuthStore("HANDLE_CLEAR_VERIFY_OTP_STORE"))
                    setButtonOTP(true);
                    setOtpVerify(true);
                    setNewPassword('');
                    setConfirmPassword('');

                }
            }
        }
    }, [
        verifyOtpRes,
        verifyOtpError,
        verifyOtpPending
    ]);

    const forgotPasswordFun = async () => {
        let data = {
            email_mobile: email,
            new_password: newPassword,
            confirm_password: confirmPassword,

        }
        dispatch(authActions.forgotPasswordRequest(data))
    }

    useEffect(() => {
        if (!forgotPasswordPending) {
            if (forgotPasswordError) {
                dispatch(authActions.handleClearAuthStore("HANDLE_CLEAR_FORGOT_PASSWORD_STORE"))
                return;
            }
            if (forgotPasswordRes) {
                if (forgotPasswordRes?.code === 200) {
                    dispatch(authActions.handleClearAuthStore("HANDLE_CLEAR_FORGOT_PASSWORD_STORE"))
                    setOtpSent(false)
                    setOtpVerify(false)
                    setEmail("")
                    toggleForgotPasswordModal()

                }
            }
        }
    }, [
        forgotPasswordRes,
        forgotPasswordError,
        forgotPasswordPending
    ]);
    useEffect(() => {
        const interval = setInterval(() => {
            if (seconds > 0) {
                setSeconds(seconds - 1);
            }

            if (seconds === 0) {
                if (minutes === 0) {
                    clearInterval(interval);
                } else {
                    setSeconds(59);
                    setMinutes(minutes - 1);
                }
            }
        }, 1000);

        return () => {
            clearInterval(interval);
        };
    });
    return (
        <>
            <div className="modal fade " id='' style={{ display: isShow ? "unset !important" : "none !important", opacity: isShow ? "unset" : "0" }}>
                <div className="modal-dialog modal-dialog-centered">
                    <div class="modal-content rounded-1 ">
                        <div class="modal-header ">
                            <h1 class="modal-title fs-5 fw-bold text-primary">{t("headings.forgotPassword")}</h1>
                            <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close" onClick={() => toggleForgotPasswordModal()}></button>
                        </div>
                        <div class="modal-body pt-0">
                            <form className='mt-2 mb-2'>
                                <div className="form-group">
                                    <label for="exampleInputEmail1">Email Address</label>
                                    <input type="email" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" onChange={(e) => onEmailChange(e)} value={email} placeholder="Enter registered email" />
                                </div>
                                {otpSent &&
                                    <div className="form-group">
                                        <label for="otp">Enter OTP {buttonOTP ? (
                                            <a className="badge badge-success text-lg" style={{ marginLeft: "5px", color: "white" }}>
                                                Verified
                                            </a>
                                        ) : (
                                            ''
                                        )}</label>
                                        <td
                                            style={{
                                                paddingTop: '1.5%',
                                                paddingBottom: '1.5%',
                                            }}
                                        >
                                            <OtpInput
                                                className="otp-spacing"
                                                inputStyle={{
                                                    width: '50%',
                                                    height: '150%',
                                                    color: '#212529',
                                                    backgroundClip: 'padding-box',
                                                    border: '1px solid #ced4da',
                                                    borderRadius: '0.375rem',
                                                    backgroundColor: buttonOTP
                                                        ? 'lightgray'
                                                        : '#fff',
                                                }}
                                                isDisabled={buttonOTP}
                                                value={otp}
                                                onChange={e => checkOtp(e)}
                                                numInputs={6}
                                                separator={<span></span>}
                                                isInputNum={true}
                                                renderInput={props => <input {...props} />}
                                            />
                                        </td>
                                    </div>
                                }
                                {otpVerify &&
                                    <>
                                        <div className="form-group">
                                            <label for="newpassword">New Password</label>
                                            <div className="position-relative eyeContt2">
                                                <input className="form-control" type={passwordShown ? "text" : "password"} onChange={(e) => setNewPassword(e.target.value)} value={newPassword} placeholder="New Password" />
                                                <i class="fa fa-eye" aria-hidden="true" style={{ left: '435px' }} onClick={() => setPasswordShown(passwordShown ? false : true)}></i>
                                            </div>
                                        </div>
                                        <div className="form-group">
                                            <label for="confirmpassword">Confirm Password</label>
                                            <div className="position-relative eyeContt2">
                                                <input type={passwordShown1 ? "text" : "password"} className="form-control" onChange={(e) => setConfirmPassword(e.target.value)} value={confirmPassword} placeholder="Confirm Password" />
                                                <i class="fa fa-eye" aria-hidden="true" style={{ left: '435px' }} onClick={() => setPasswordShown1(passwordShown1 ? false : true)}></i>
                                            </div>
                                        </div>
                                    </>
                                }
                                {otpSent === false &&
                                    <button
                                        type="button" className="btn btn-primary mt-3"
                                        onClick={() => sendOtpFun()}
                                    >Send OTP
                                    </button>
                                }
                                {/* {otpSent == true && otpVerify == false &&
                            <button type="button" className="btn btn-primary" onClick={otpVerifyFun}>Verify OTP</button>
                        } */}
                            </form>
                        </div>
                        <div class="modal-footer pt-0">
                            {otpSent === true && otpVerify === false &&
                                <button type="button" className="btn btn-primary" onClick={minutes > 0 || seconds > 0 ? null : sendOtpFun}>{minutes > 0 || seconds > 0 ? (<> {minutes < 10 ? `0${minutes}` : minutes}:
                                    {seconds < 10 ? `0${seconds}` : seconds}</>) : 'Resend OTP'}</button>
                            }
                            {otpVerify === true &&
                                <button type="button" className="btn btn-primary" onClick={forgotPasswordFun}>Change Password</button>
                            }
                        </div>
                    </div>
                </div>
            </div>
            {isShow && <div className="modal-backdrop fade show"></div>}
        </>
    )
}

export default ForgotPasswordModal
